.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 28px 20px;
  max-width: 600px;
}

.humans__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 250px;
}

.humans__item {
  display: block;
  height: 70px;
  width: 110px;
  border-radius: 5px;

  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), #000000;
  color: #fff;
  font-weight: 700;
}

.control {
  display: flex;
  transform: translate(50%, 50%);
}

.info {
  height: 300px;
  width: 240px;
  border: 2px solid #000;
  border-radius: 10px;
  padding: 20px 10px;
}

